import { TeacherSubjectsService } from "app/student/services/teacherSubjects.service";
import { Component, OnInit } from "@angular/core";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { OwlOptions } from "ngx-owl-carousel-o";
import { MessagesService } from "app/shared/services/messages.service";

@Component({
  selector: "app-home-index",
  templateUrl: "./home-index.component.html",
  styleUrls: [
    "./home-index.component.scss",
   
  ],
})
export class HomeIndexComponent implements OnInit {
  teachers?: any = [];
  constructor(
    public authservice: AuthService,
    private TeacherSubServ: TeacherSubjectsService,
    private MessagesService: MessagesService
  ) {}

  ngOnInit(): void {
    this.TeacherSubServ.ReturnHomeTeachers().subscribe((res: any) => {
      this.teachers = res;
      //console.log(res)
    });
  }

  customOptions: OwlOptions = {
    autoplay: false,
    loop: false,
    rewind: true,
    margin: 10,
    dots: false,
    navSpeed: 300,
    navText: [
      '<i class="fas fa-arrow-right"></i>',
      '<i class="fas fa-arrow-left"></i>',
    ],
    responsive: {
      0: {
        items: 2,
      },
      400: {
        items: 2,
      },
      740: {
        items: 4,
      },
      940: {
        items: 5,
      },
    },
    nav: false,
  };
}
